.type-animation {
    animation: type 4s steps(29);
    overflow: hidden;
    white-space: nowrap;
    font-family: consolas;
    font-size: 25px;
    border-right: 4px solid black;
    width: 29ch;
}

@keyframes type {
    0% {
        width: 0ch;
    }
    100% {
        width: 29ch;
    }
}